import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VhAlgorithm, VhQuerySales } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { VhType } from 'src/app/sales/interface/vh-type';
import { LanguageService } from 'src/app/sales/services/language.service';

@Component({
  selector: 'app-by-product-app',
  templateUrl: './by-product-app.component.html',
  styleUrls: ['./by-product-app.component.scss']
})
export class ByProductAppComponent implements OnInit {

  listProductApp: Array<any> = [] //sp của app chứa tikis[]
  listSubProductApp: Array<any> = [] //sp của app chứa tikis[]
  /**----------------------------------- */
  infoShop: any = {}
  searchValue: any;
  heightScroll: any;
  /**
   * Modal chọn sản phẩm liên kết
   */
  visible_affiliate: boolean = false;
  searchOnTiki: string = ''
  listProductSelected: Array<any> = []
  id_product_selected: string = ''
  setHeight = { "max-height": "500px", "height": "500px" }
  id_branch: string = this.vhQuerySales.getDefaultBranch()._id
  constructor(
    private router: Router,
    public lang: LanguageService,
    public vhComponent: VhComponent,
    public vhAlgorithm: VhAlgorithm,
    public vhQuerySales: VhQuerySales,
    private cdRef: ChangeDetectorRef,
  ) {
  }

  ngOnInit(): void {
    this.getData()
  }
  getData() {
    this.infoShop = this.vhQuerySales.getlocalShopListTiki()[0];
    this.changeShop(this.infoShop._id)

  }
  /**Chọn shop và lọc sản phẩm theo shop đó */
  changeShop(id) {
    let localProducts: Array<any> = this.vhQuerySales.getlocalDetailProducts(VhType.PRODUCT).filter(e => !e.delete_hidden)
    this.listProductApp = localProducts.map(item => {
      if (item[this.id_branch] && item[this.id_branch].tikis && item[this.id_branch].tikis.length) { //filter những sp tiki thuộc id tiki đã chọn
        item[this.id_branch].tikis = [...item[this.id_branch].tikis.filter(prod => prod.id_shop == id)]
      }
      return item
    })
    this.listSubProductApp = [...this.listProductApp];
  }
  trackByFn(index, item) {
    return index;
  }
  goBack() {
    this.router.navigate(['/sales/dashboard/manage/ecommerce/tiki/update-stock'])
  }
  //search tên sản phẩm trong app viethas
  search(value: string) {
    if (value.length) {
      let val: string = this.vhAlgorithm.changeAlias(value.trim().toLowerCase())
      this.listProductApp = this.vhAlgorithm.searchList(val, [...this.listSubProductApp], ['name'])
    } else this.listProductApp = [...this.listSubProductApp]
  }
  ngAfterViewChecked() {
    if (document.querySelector("#by-product-app-stock") && document.querySelector(".ant-table-thead") && document.querySelector(".by-product-app-stock-header") && document.querySelector(".ant-table-pagination")) {
      this.heightScroll = document.querySelector("#by-product-app-stock").clientHeight - document.querySelector(".ant-table-thead").clientHeight
        - document.querySelector(".by-product-app-stock-header").clientHeight - document.querySelector(".ant-table-pagination").clientHeight - 60 + "px";
    }
    this.cdRef.detectChanges();
  }
  /**
   * Show/hide sản phẩm con liên kết với sp của app
   * @param productApp 
   */
  renderProductTikis(productApp) {
    let productTiki = this.vhQuerySales.getlocalProductListTiki(this.infoShop['_id'])
    if (!productApp.expand && !productApp.loaded) {
      if (productApp[this.id_branch] && productApp[this.id_branch].tikis && productApp[this.id_branch].tikis.length) {
        for (let indexProd in productApp[this.id_branch].tikis) {
          let prodTiki = productTiki.find(item => item.product_id == productApp[this.id_branch].tikis[indexProd].product_id)
          if (prodTiki)
            productApp[this.id_branch].tikis[indexProd] = { ...prodTiki, ...productApp[this.id_branch].tikis[indexProd], loading : false }
        }
        productApp.loaded = true;
      }
    }
    productApp.expand = !productApp.expand;
  }
  checkStock(product, event: any) {
    if (typeof event == 'string')
      delete product.newQuantity
    else
      product.newQuantity = event
  }
  /**Cập nhật từng sp , sau đó get sp từ tiki về kt
   * nếu quantity = với quantity trên tiki thì cập nhật thành công
  */
  updateQuantity(product, productApp) {
    product.loading = true;
    this.vhQuerySales.updateProductByJSONTiki(this.renderFormUpdate(product), this.infoShop['access_token']).then((res: any) => {
      if (res.state == 'approved') {
        this.vhQuerySales.getProductByIdTiki(this.infoShop['access_token'], product.product_id, this.infoShop['_id']).then((producttiki: any) => {
          product.loading = false
          if (producttiki && this.checkQuantityAfterUpdate(product, producttiki)) {
            product['dateUpdateStock'] = new Date().toISOString()
            this.vhQuerySales.updateMergeTikiProduct(productApp['id_subproduct'] ? productApp['id_subproduct'] : productApp['_id'], product['product_id'], { dateUpdateStock: product['dateUpdateStock'] }, this.infoShop['_id']).then((bool: boolean) => {
              product.loading = false
              if (bool)
                this.vhComponent.alertMessageDesktop("success", this.lang.translate("Update successfully"))
              else this.vhComponent.alertMessageDesktop("error", this.lang.translate("Update failed"))
            })
          } else {
            product.loading = false
            this.vhComponent.alertMessageDesktop("error", this.lang.translate("Update failed"))
          }
        }).finally(() => product.loading = false)
      } else {
        product.loading = false
        this.vhComponent.alertMessageDesktop("error", this.lang.translate("Update failed"))
      }
    });
  }
  checkQuantityAfterUpdate(product, producttiki) {
    if (product.newQuantity == producttiki.inventory.quantity_sellable) {
      product.inventory = producttiki.inventory
      product.quantity = producttiki.inventory.quantity
      delete product.newQuantity
      return true
    } else return false
  }
  renderFormUpdate(product) {
    let json = {
      "product_id": product.product_id.toString(),
      "quantity": product.newQuantity
    }
    
    return json
  }
}
